<template>
    <div>
        <div data-v-35b90bdf="" class="page">
            <div data-v-152ab910="" data-v-35b90bdf="" class="sub-header">
                <div data-v-152ab910="" class="back" @click="$router.go(-1)"></div>
                <div data-v-152ab910="" class="title">修改收货地址</div><!----></div>
            <div data-v-35b90bdf="" class="main">
                <ul data-v-35b90bdf="">
                    <li data-v-35b90bdf="">收货人</li>
                    <li data-v-35b90bdf="">
                        <input data-v-35b90bdf="" type="text" placeholder="收货人姓名" v-model="name">
                    </li>
                </ul>
                <ul data-v-35b90bdf="">
                    <li data-v-35b90bdf="">联系方式</li>
                    <li data-v-35b90bdf="">
                        <input data-v-35b90bdf="" type="text" placeholder="联系人手机号" v-model="cellphone">
                    </li>
                </ul>
                <ul data-v-35b90bdf="">
                    <li data-v-35b90bdf="">所在地区</li>
                    <li data-v-35b90bdf="">
                        <input data-v-35b90bdf="" :value="showArea" type="text" @click="isArea=true"
                               placeholder="请选择所在地区"
                               class="area">
                    </li>
                </ul>
                <ul data-v-35b90bdf="">
                    <li data-v-35b90bdf="">详细地址</li>
                    <li data-v-35b90bdf="">
                        <input data-v-35b90bdf="" v-model="address" type="text" placeholder="街道详细地址">
                    </li>
                </ul>
                <ul data-v-35b90bdf="">
                    <li data-v-35b90bdf="">设置为默认地址</li>
                    <li data-v-35b90bdf="">
                        <input data-v-35b90bdf="" v-model="isDefault" type="checkbox">
                    </li>
                </ul>
                <button data-v-35b90bdf="" type="button" class="submit-save" @click="onModAddress"> 修改</button>
            </div><!---->
            <van-popup v-model="isArea">
                <van-area :area-list="areaList" @cancel="isArea=false" @confirm="selectArea"/>
            </van-popup>
        </div>
    </div>
</template>

<script>
    import Vue from 'vue';
    import {Toast, Area, Popup} from 'vant';
    import areaData from '../../assets/data/area';
    import {mapActions} from "vuex"

    Vue.use(Area);
    Vue.use(Popup);
    export default {
        data() {
            return {
                name: "",
                cellphone: "",
                showArea: "",
                address: "",
                province: "",
                city: "",
                area: "",
                isDefault: false,
                areaList: areaData,
                isArea: false,
                aid: this.$route.query.aid ? this.$route.query.aid : ""
            }
        },
        mounted() {
            document.title = this.$route.meta.title
        },
        created() {

            //安全认证
            this.$utils.safeUser(this);
            //获取收货地址信息
            this.getAddressInfo({
                aid: this.aid, success: (res) => {
                        this.name = res.data.name ;
                        this.cellphone = res.data.cellphone;
                        this.showArea = res.data.province + "" + res.data.city + "" + res.data.area;
                        this.address = res.data.address;
                         this.isDefault = res.data.isDefault==='1'?true:false;

                }
            });
        },
        methods: {
            ...mapActions({
                //修改收货地址
                getModAddressInfo: "address/getModAddressInfo",
                //获取收货地址信息
                getAddressInfo: "address/getAddressInfo"
            }),
            //分隔地区为市 区
            selectArea(val) {
                this.isArea = false;
                let tmpVal = [];
                if (val.length > 0) {
                    for (let i = 0; i < val.length; i++) {
                        tmpVal.push(val[i].name);
                    }
                    this.province = tmpVal[0];
                    this.city = tmpVal[1];
                    this.area = tmpVal[2];
                }
                this.showArea = tmpVal.join(" ");
            },
            //添加收货地址
            onModAddress() {

                if (this.name.match(/^\s*$/)) {
                    Toast("请输入收货人姓名");
                    return;
                }
                if (this.cellphone.match(/^\s*$/)) {
                    Toast("请输入手机号");
                    return;
                }
                if (!this.cellphone.match(/^1[0-9][0-9]\d{8}$/)) {
                    Toast("您输入的手机号格式不正确");
                    return;
                }
                if (this.showArea.match(/^\s*$/)) {
                    Toast("请输入所在地区");
                    return;
                }
                if (this.address.match(/^\s*$/)) {
                    Toast("请输入街道详细地址");
                    return;
                }
                this.getModAddressInfo({
                    aid:this.aid,
                    name: this.name,
                    cellphone: this.cellphone,
                    province: this.province,
                    city: this.city,
                    area: this.area
                    ,
                    address: this.address,
                    isdefault: this.isDefault ? "1" : "0",
                    success:()=>{
                        this.$router.go(-1)
                    }
                })
            }
        }
    }
</script>

<style scoped>
    .page {
        width: 100%;
        height: 100vh;
        overflow: hidden;
        background-color: #FFFFFF;
    }

    .sub-header {
        width: 100%;
        height: 1rem;
        background-color: #FFFFFF;
        display: flex;
        display: -webkit-flex;
        align-items: center;
        -webkit-align-items: center;
        border-bottom: 1px solid #EFEFEF;
        position: fixed;
        z-index: 10;
        left: 0;
        top: 0;
        justify-content: space-between;
        padding: 0px 0.1rem;
        box-sizing: border-box
    }

    .sub-header .back {
        width: 0.8rem;
        height: 0.8rem;
        background-image: url("../../assets/images/home/goods/back.png");
        background-size: 100%;
        background-repeat: no-repeat;
        background-position: center;
    }

    .sub-header .title {
        width: 79%;
        height: auto;
        font-size: 0.32rem;
        text-align: center;
        position: absolute;
        z-index: 1;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%)
    }

    .sub-header .right-btn {
        width: auto;
        height: auto;
        font-size: 0.32rem;
    }

    .main {
        width: 100%;
        margin-top: 1rem;
    }

    .main ul {
        width: 100%;
        height: 1.02rem;
        border-bottom: #EFEFEF 1px solid;
        display: flex;
        display: -webkit-flex;
        align-items: center;
        -webkit-align-items: center;
    }

    .main ul li {
        font-size: 0.32rem;
        margin-left: 0.3rem;
    }

    .main ul li:nth-child(2) {
        width: 62%;
        height: 100%;
    }

    .main ul li input[type='text'] {
        width: 100%;
        height: 95%;
        font-size: 0.32rem;
    }

    .main ul li .area {
        font-size: 0.28rem !important;
    }

    .main ul li input[type='checkbox'] {
        width: 0.4rem;
        height: 0.4rem;
        margin-top: 0.3rem;
    }

    .main .submit-save {
        width: 85%;
        height: 0.8rem;
        background-color: #FCB40A;
        border-radius: 4px;
        margin: 0 auto;
        display: block;
        border: 0 none;
        outline: none;
        font-size: 0.32rem;
        color: #FFFFFF;
        margin-top: 0.4rem;
    }
</style>